.call {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  flex-direction: column;
  max-width: 440px;
  font-family: 'TTLakesMedium';
  font-size: 24px;
  color: #1b1c21;
  background-color: #ffffff;
  box-shadow: 16px 0px 52px 0px #0e185033;
  height: 100vh;
  z-index: 10;
  -webkit-animation: slideFromRight 0.3s ease-in-out;
          animation: slideFromRight 0.3s ease-in-out; }

.call-closing {
  -webkit-animation: slideToRight 0.3s ease-in-out forwards;
          animation: slideToRight 0.3s ease-in-out forwards; }

.call--active {
  display: flex; }

#call-close {
  margin-top: 24px;
  margin-left: 5%;
  width: 24px;
  height: 24px;
  transition: 0.3s; }

#call-close:hover {
  transform: scale(1.1);
  cursor: pointer; }

.call__title {
  margin-left: 5%; }

.call__title::before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  background-color: #41f6d7;
  height: 40px;
  width: 4px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px; }

@media (min-width: 1120px) {
  #call-close {
    position: absolute;
    margin-left: 0;
    left: -54px; } }
