.send {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  flex-direction: column;
  max-width: 440px;
  font-family: 'TTLakesMedium';
  font-size: 24px;
  color: #1b1c21;
  background-color: #ffffff;
  box-shadow: 16px 0px 52px 0px #0e185033;
  height: 100vh;
  z-index: 10;
  -webkit-animation: slideFromRight 0.3s ease-in-out;
          animation: slideFromRight 0.3s ease-in-out; }

.send--active {
  display: flex; }

.form {
  display: flex;
  flex-direction: column; }

.send__title {
  margin-left: 5%; }

.send__title::before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  background-color: #41f6d7;
  height: 40px;
  width: 4px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px; }

.form__description {
  margin-left: 5%;
  color: #7e7e82;
  font-size: 14px; }

.form__input {
  box-sizing: border-box;
  display: inline-block;
  padding-left: 16px;
  height: 48px;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 16px;
  border: 1px solid #eaeaea;
  font-size: 16px;
  font-family: 'TTLakesRegular';
  border-radius: 12px; }

.form__link {
  color: #ff3e79;
  text-decoration: none; }

.form__link:hover {
  text-decoration: underline; }

.form__textarea {
  height: 118px;
  padding-top: 12px;
  margin-bottom: 19px; }

#send-close {
  margin-top: 24px;
  margin-left: 5%;
  width: 24px;
  height: 24px;
  transition: 0.3s; }

#send-close:hover {
  transform: scale(1.1);
  cursor: pointer; }

.form__submit::after {
  content: '';
  background-image: url(./static/goside.svg);
  width: 24px;
  height: 24px;
  display: block;
  position: absolute;
  right: 5px;
  top: 3px; }

.form__submit {
  box-sizing: border-box;
  position: relative;
  background-color: #ff3e79;
  padding-left: 12px;
  padding-right: 12px;
  height: 32px;
  width: 136px;
  border-radius: 25px;
  margin-left: auto;
  margin-right: 5%;
  margin-top: 24px;
  color: #ffffff;
  text-transform: uppercase;
  font-family: 'TTLakesBold';
  font-size: 12px;
  text-align: left; }

.send-closing {
  -webkit-animation: slideToRight 0.3s ease-in-out forwards;
          animation: slideToRight 0.3s ease-in-out forwards; }

@media (min-width: 1120px) {
  .send {
    margin-left: 320px; }
  #send-close {
    position: absolute;
    margin-left: 0;
    left: -54px; } }

@-webkit-keyframes slideFromRight {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

@keyframes slideFromRight {
  from {
    transform: translateX(100%); }
  to {
    transform: translateX(0); } }

@-webkit-keyframes slideToRight {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(120%); } }

@keyframes slideToRight {
  from {
    transform: translateX(0); }
  to {
    transform: translateX(120%); } }
