.service-aside__menu-element--active {
  display: flex;
  align-items: center;
  color: #7e7e82;
  cursor: default; }

.service-aside__menu-element--active::before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  background-color: #41f6d7;
  height: 32px;
