button {
  padding: 0;
  background-color: transparent;
  border: none; }

.service-header__button,
.service-main__button,
.service-info__read-next {
  transition: 0.3s;
  cursor: pointer;
  text-decoration: none; }

.service-header__button:hover,
.service-main__button:hover,
.service-info__read-next:hover {
  transform: scale(1.1);
  color: #41f6d7;
  transition: 0.3s; }

body {
  margin: 0;
  background-color: #f8f8f8; }

.service-header__left,
.service-header__right {
  display: flex;
  gap: 10px; }

.service-header {
  display: flex;
  margin: 0 5px;
  height: 88px;
  border-bottom: 1px solid #d9fff5;
  background-color: #fff;
  padding: 0 5%; }

.service-header__button {
  max-height: 50px;
  margin: auto 0; }

.service-main {
  padding-top: 24px;
  width: 100%; }

.service-main__title {
  font-family: 'TTLakesBold', sans-serif;
  font-size: 28px;
  height: 40px;
  margin: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-start: 0;
          margin-inline-start: 0;
  -webkit-margin-end: 0;
          margin-inline-end: 0;
  margin-left: 5%; }

.service-main__flex-container {
  margin-bottom: 24px; }

.service-main__title::before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  background-color: #41f6d7;
  height: 40px;
  width: 4px;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px; }

.service-main__nav {
  display: flex;
  margin-left: 5%;
  align-items: center;
  overflow-x: scroll;
  white-space: nowrap; }

.service-main__nav::-webkit-scrollbar {
  display: none; }

.service-main__button {
  font-family: 'TTLakesMedium', sans-serif;
  font-size: 16px;
  border: none;
  background: transparent;
  color: #7e7e82;
  padding: 0 16px;
  padding-top: 7px;
  padding-bottom: 7px; }

.service-main__button_active {
  border: 2px solid #b8ffec;
  border-radius: 6px;
  background-color: #ffffff;
  padding-top: 7px;
  padding-bottom: 7px; }

.service-main__button_active:hover {
  color: #7e7e82;
  transform: scale(1);
  cursor: default; }

.service-info__text {
  font-family: 'TTLakesRegular', sans-serif;
  color: #1b1c21;
  font-size: 14px;
  margin-left: 5%;
  height: 100px;
  overflow: hidden;
  transition: 1s; }

.service-info__text--active {
  height: auto; }

.service-info__read-next--active::before {
  transform: rotate(180deg); }

.service-info__read-next {
  display: flex;
  width: 200px;
  align-items: center;
  color: #1b1c21;
  height: 24px;
  font-size: 16px;
  margin-top: 16px;
  margin-bottom: 16px;
  transition: 1s; }

.service-info__read-next::before {
  content: '';
  margin-left: 5%;
  margin-right: 8px;
  display: inline-block;
  background-image: url("/src/img/show-more.svg");
  height: 24px;
  width: 24px;
  transition: 1s; }

.service-info__image {
  width: 100%;
  padding-bottom: 51px; }

/*MOBILE Ver*/
@media (max-width: 767px) {
  .mobile-hidden {
    display: none; }
  .service-info__read-next {
    margin-left: 5%; }
  .service-header {
    display: flex;
    position: relative; }
  .logo {
    margin-left: 5px; }
  .service-header__left::before {
    content: '';
    background: #eaeaea;
    width: 2px;
    height: 32px;
    position: relative;
    left: 190px;
    top: 30px; }
  .service-header__right {
    flex: 1;
    justify-content: flex-end; }
  .service-main__nav {
    display: flex;
    margin: 0;
    align-items: center;
    overflow-x: scroll;
    white-space: nowrap; }
  .service-main__nav::after {
    content: '';
    display: block;
    position: fixed;
    height: 81px;
    width: 24px;
    left: 0px;
    z-index: 2;
    background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
    transform: rotate(180deg); }
  .service-main__nav::before {
    content: '';
    display: block;
    position: fixed;
    height: 81px;
    width: 24px;
    right: -17px;
    z-index: 2;
    background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
    transform: rotate(180deg); } }

/*TABLET Ver*/
@media (min-width: 768px) and (max-width: 1120px) {
  .tablet-hidden {
    display: none; }
  .mobile-hidden {
    position: relative;
    top: 25%; }
  .mobile-hidden::after {
    content: '';
    background: #eaeaea;
    width: 2px;
    height: 32px;
    position: absolute;
    right: -5px;
    top: 5px; } }

@media (min-width: 768px) {
  .service-header .logo {
    margin-right: 32.55%; }
  .service-info {
    display: flex;
    justify-content: space-around;
    margin-top: 32px;
    margin-left: 5%;
    margin-right: 5%;
    max-width: 93.7%; }
  .service-info__image {
    border-radius: 5px;
    width: 50%;
    height: 100%;
    margin-left: 6.25%; }
  .service-info__text {
    margin: 0; }
  .service-info__read-next::before {
    margin-left: 0; }
  .logo {
    margin-left: 5px; }
  .service-header__left {
    flex: 1; }
  .service-header__left::after {
    content: '';
    background: #eaeaea;
    width: 2px;
    height: 32px;
    position: relative;
    top: 30px;
    right: 120px; }
  .service-header__right {
    flex: 1;
    justify-content: flex-end; }
  .service-main__nav {
    display: flex;
    margin: 0;
    align-items: center;
    overflow-x: scroll;
    white-space: nowrap; }
  .service-main__nav::after {
    content: '';
    display: block;
    position: fixed;
    height: 81px;
    width: 24px;
    left: 0px;
    z-index: 2;
    background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
    transform: rotate(180deg); }
  .service-main__nav::before {
    content: '';
    display: block;
    position: fixed;
    height: 81px;
    width: 24px;
    right: -17px;
    z-index: 2;
    background: linear-gradient(270deg, #f8f8f8 16.84%, rgba(248, 248, 248, 0.0001) 100%);
    transform: rotate(180deg); } }

/*DESKTOP Ver*/
@media (min-width: 1120px) {
  .service-header {
    visibility: hidden;
    position: relative;
    height: 0;
    transition: 0; }
  .service-header {
    box-shadow: none; }
  .service {
    margin-left: 320px; }
  .service-header__button:not(.service-header__button_desktop) img {
    visibility: hidden; }
  .service-header__button_desktop {
    visibility: visible;
    display: flex;
    align-items: center;
    position: relative;
    color: #1b1c21;
    font-family: 'TTLakesMedium';
    font-size: 16px;
    right: 5%;
    top: 42px;
    transition: 0.3s; }
  .service-header__button {
    flex: 1;
    gap: 10px;
    justify-content: space-between;
    justify-content: flex-end; }
  .service-header__button_label {
    margin-right: 8px; }
  .service-main__nav {
    overflow: visible;
    flex-wrap: wrap; }
  .service-main {
    padding-top: 42px; }
  .service-main__flex-container {
    display: flex;
    align-items: center; }
  .service-info__image {
    height: 307px;
    width: 531px; }
  .repaiir {
    display: inline-flex;
    white-space: nowrap; }
  .statuus {
    display: inline-flex;
    white-space: nowrap;
    left: 20px; }
  .service-main__title::before {
    left: 320px; } }
